import React from "react";
import routerMap from "../../routers/mapRoutersToProps";
import './style.css'

class Show extends React.Component {
  render() {
    return (
      <div className="wy_showbox">
        <div className="wy_shownav">
          <span>展示信息</span>
          <ul className="wy_shownavs">
            <li onClick={()=>{this.navigates('/exam/show/user')}}>展示用户</li>
            <li onClick={()=>{this.navigates('/exam/show/identity')}}>展示类型</li>
          </ul>
        </div>
        <div className="wy_routerbox">{routerMap(this.props.routers)}</div>
      </div>
    );
  }
  navigates=(type)=>{
    this.props.history.push(type)
  }
}

export default Show;
