import React from "react";
import "./index.css";
import { Table, Divider, Button } from "antd";
import { connect } from 'dva';
import http from '@/http'

const columns = [
  {
    title: "类型ID",
    dataIndex: "questions_type_id",
    key: "name",
    render: text => <span>{text}</span>
  },
  {
    title: "类型名称",
    dataIndex: "questions_type_text",
    key: "nums"
  },
  {
    title: "操作",
    key: "action",
    render: (text, record) => (
      <span>
        <a href="/">详情</a>
        <Divider type="vertical" />
        <a href="/">编辑</a>
        <Divider type="vertical" />
        <a href="/">删除</a>
      </span>
    )
  }
];


class ClassManger extends React.Component {
  constructor(){
    super();
    this.state =  {
      data:[]
    }
  }
  componentDidMount(){
    http.get('/exam/getQuestionsType').then(res=>{
      let { data } = res;
      data = data.map((item,i)=>{
        return {...item,key:i}
      })
      console.log(data)
      this.setState({
        data
      })
    })
  }
  render() {
    return (
      <div className="syh-container">
        <section className="syh-classInfo">
          <Table
            pagination={{ pageSize: 6 }}
            columns={columns}
            dataSource={this.state.data}
          />
        </section>
      </div>
    );
  }
}
const mapStateToProps = (state)=>{
  console.log(state)
  return state
}
const mapDispatchToProps = (dispatch) =>{
    return {
      update(){
        return dispatch({
          type:'products/borderland',
          payload:'sss'
        })
      }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(ClassManger);
